async function executeRemoveBorder(context, params) {
  console.log('executeRemoveBorder called with params:', params);
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  
  try {
    let targetRange;
    
    // Determine the range to apply border removal
    if (params.range) {
      // Handle special case for entire sheet
      if (params.range === 'all' || params.range === '전체') {
        console.log('Removing borders from entire sheet');
        // Use getUsedRangeOrNullObject to get the actual used range
        const usedRange = worksheet.getUsedRangeOrNullObject();
        usedRange.load("isNullObject");
        await context.sync();
        
        if (usedRange.isNullObject) {
          console.log('No used range in the sheet, nothing to remove borders from');
          return { success: true, message: '시트에 데이터가 없어 테두리를 제거할 필요가 없습니다.' };
        } else {
          // Expand the used range to ensure we get all cells that might have borders
          usedRange.load(["rowCount", "columnCount", "rowIndex", "columnIndex"]);
          await context.sync();
          
          // Get a slightly larger range to ensure we capture all borders
          const expandedRows = Math.min(usedRange.rowCount + 100, 1048576);
          const expandedCols = Math.min(usedRange.columnCount + 20, 16384);
          targetRange = worksheet.getRangeByIndexes(0, 0, expandedRows, expandedCols);
        }
      } else if (params.range.match(/^[A-Z]+:[A-Z]+$/)) {
        // Handle column ranges like "C:C"
        console.log('Handling column range:', params.range);
        const usedRange = worksheet.getUsedRange();
        if (!usedRange) {
          return { success: false, error: '데이터가 없습니다.' };
        }
        
        usedRange.load(["rowCount", "columnCount"]);
        await context.sync();
        
        // Get the column letter from the range (e.g., "C" from "C:C")
        const columnLetter = params.range.split(':')[0];
        const columnIndex = columnLetter.charCodeAt(0) - 65;
        
        // Get the entire used portion of the column
        targetRange = worksheet.getRangeByIndexes(0, columnIndex, usedRange.rowCount, 1);
      } else {
        // Handle specific ranges like "A1:B10"
        targetRange = worksheet.getRange(params.range);
      }
    } else {
      // If no range specified, use the selected range
      targetRange = context.workbook.getSelectedRange();
    }
    
    // Load the range
    targetRange.load(["address", "rowCount", "columnCount"]);
    await context.sync();
    
    console.log('Target range for border removal:', {
      address: targetRange.address,
      rowCount: targetRange.rowCount,
      columnCount: targetRange.columnCount
    });
    
    // Determine which borders to remove
    const borderType = params.borderType || 'all';
    
    if (borderType === 'all') {
      // Remove all borders
      targetRange.format.borders.getItem(Excel.BorderIndex.edgeLeft).style = Excel.BorderLineStyle.none;
      targetRange.format.borders.getItem(Excel.BorderIndex.edgeRight).style = Excel.BorderLineStyle.none;
      targetRange.format.borders.getItem(Excel.BorderIndex.edgeTop).style = Excel.BorderLineStyle.none;
      targetRange.format.borders.getItem(Excel.BorderIndex.edgeBottom).style = Excel.BorderLineStyle.none;
      targetRange.format.borders.getItem(Excel.BorderIndex.insideHorizontal).style = Excel.BorderLineStyle.none;
      targetRange.format.borders.getItem(Excel.BorderIndex.insideVertical).style = Excel.BorderLineStyle.none;
    } else {
      // Remove specific border
      switch (borderType) {
        case 'right':
          targetRange.format.borders.getItem(Excel.BorderIndex.edgeRight).style = Excel.BorderLineStyle.none;
          break;
        case 'left':
          targetRange.format.borders.getItem(Excel.BorderIndex.edgeLeft).style = Excel.BorderLineStyle.none;
          break;
        case 'top':
          targetRange.format.borders.getItem(Excel.BorderIndex.edgeTop).style = Excel.BorderLineStyle.none;
          break;
        case 'bottom':
          targetRange.format.borders.getItem(Excel.BorderIndex.edgeBottom).style = Excel.BorderLineStyle.none;
          break;
        default:
          return { success: false, error: `알 수 없는 테두리 유형: ${borderType}` };
      }
    }
    
    await context.sync();
    
    return {
      success: true,
      message: `${targetRange.address} 범위의 ${borderType === 'all' ? '모든' : borderType} 테두리가 제거되었습니다.`
    };
    
  } catch (error) {
    console.error('Error in executeRemoveBorder:', error);
    return {
      success: false,
      error: `테두리 제거 중 오류 발생: ${error.message}`
    };
  }
}